export default {
  // vue组件实例的配置项
  methods: {
    loginConfirm () {
      if (!this.$store.getters.token) {
        this.$dialog.confirm({
          title: '提示',
          message: '需要先登录才能继续操作哦',
          confirmButtonText: '去登录',
          confirmButtonColor: '#c21401',
          cancelButtonText: '再逛逛'
        }).then(() => {
          // 如果希望登录成功后跳转回来  需要传参
          // this.$route.fullPath (会包含查询参数)
          this.$router.replace(`/login?backUrl=${this.$route.fullPath}`)
        }).catch(() => {})
        return false
      }
      return true
    }
  }
}
