<template>
    <div>
      <nav-bar title="商品详情" :border="false"></nav-bar>
      <van-swipe @change="swiperChange" :autoplay="3000">
        <van-swipe-item v-for="item in detail.goods_images" :key="item.file_id"><img :src="item.external_url" alt=""></van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">{{ current + 1 }} / {{ detail.goods_images && detail.goods_images.length }}</div>
        </template>
      </van-swipe>
      <div class="content">
        <div class="price-sales">
          <div class="price-box">
            <span class="price">￥{{ detail.goods_price_min }}</span>
            <span class="price2" v-if="detail.line_price_max !== '0.00'">￥{{ detail.line_price_max}}</span>
          </div>
          <div class="sales">已售{{ detail.goods_sales }}件</div>
        </div>
        <div class="title">{{ detail.goods_name }}</div>
        <div class="service-wrap">
          <van-cell is-link @click="showService = true" class="service-box">
            <template #title>
              <div class="text" v-for="item in goodsService" :key="item.service_id">
                <van-icon name="passed" color="#c21401" />
                <span>{{ item.name }}</span>
              </div>
            </template>
          </van-cell>
          <van-action-sheet  v-model="showService" title=" " cancel-text="我知道了">
            <div class="content">
              <div class="service"  v-for="item in goodsService" :key="item.service_id">
                <div class="title">{{ item.name }}</div>
                <div class="text">{{ item.summary }}</div>
              </div>
            </div>
          </van-action-sheet>
        </div>
        <div class="comment-box" v-if="commentList.length>0">
          <van-cell :title="`商品评价(${total}条)`" is-link value="查看更多" :to="`/comment/${goodsId}`" />
          <div class="list">
             <div class="item" v-for="item in commentList" :key="item.comment_id" >
               <div class="top">
                 <img :src="item.user.avatar_url || defaultImg" alt="" class="img">
                 <div class="name">{{ item.user.nick_name }}</div>
                 <van-rate :value="item.score % 6 + 1" :size="16" color="#ffd21e" void-icon="star" void-color="#eee"/>
               </div>
               <div class="con">{{ item.content }}</div>
               <div class="bot">{{ item.create_time }}</div>
             </div>
          </div>
        </div>
        <div class="desc" v-html="detail.content" ref="descDom" @click="imageChagange($event)"></div>
      </div>
      <!-- 加入购物车弹窗 -->
      <van-action-sheet class="pannel-box" v-model="showPannel"  :title="mode === 'cart' ? '加入购物车' : '立即购买'">
        <div class="content">
           <div class="goods">
              <div class="left">
                <img :src="detail.goods_image" alt="">
              </div>
              <div class="right">
                <div class="price"><span>￥</span>{{ detail.goods_price_min }}</div>
                <div class="stock">库存{{ detail.stock_total }}</div>
              </div>
           </div>
           <div class="num-box">
            <div class="label">数量</div>
            <!-- v-model =>:value +  @input -->
            <count-box v-model="countNum" :stock="detail.stock_total"></count-box>
           </div>
           <div class="btn-box" >
              <div v-if="detail.stock_total > 0">
                <button class="btn btn1" v-if="mode==='cart'" @click="addCart">加入购物车</button>
                <button class="btn btn2" v-else @click="goPay">立即购买</button>
              </div>
              <div class="btn btn-none" v-else>该商品已抢完</div>
           </div>
        </div>
      </van-action-sheet>
      <van-goods-action>
        <van-goods-action-icon icon="wap-home-o" text="首页" to="/home" />
        <van-goods-action-icon icon="cart-o" :class="{'cart-badge-none': cartTotal===0 }" text="购物车" to="/cart"  :badge="cartTotal" />
        <van-goods-action-button class="btn1" type="warning" text="加入购物车"  @click="addFn"/>
        <van-goods-action-button class="btn2" type="danger" text="立即购买" @click="buyFn" />
      </van-goods-action>
    </div>
</template>

<script>
import { ImagePreview } from 'vant'
import defaultImg from '@/assets/images/user.jpg'
import CountBox from '@/components/CountBox.vue'
import loginConfirm from '@/mixins/loginConfirm'
export default {
  name: 'GoodsIndex',
  components: {
    CountBox
  },
  mixins: [loginConfirm],
  data () {
    return {
      current: 0,
      detail: {},
      commentList: [],
      goodsService: [],
      total: 0,
      showService: false,
      defaultImg,
      // defaultImg: require('@/assets/images/user.jpg')
      showPannel: false,
      mode: 'cart', // 标记弹窗状态
      countNum: 1, // 购买数量
      cartTotal: 0, // 购物车数量
      imgs: [],
      imgIndex: 0
    }
  },
  created () {
    this.getGoodsDetail()
    this.getGoodsService()
    this.getGoodsComment()
    this.getCartTotal()
  },
  activated () {
    if (!this.$route.meta.canKeep) {
      // 在这里发送请求，刷新数据
      Object.assign(this.$data, this.$options.data())
      this.getGoodsDetail()
      this.getGoodsService()
      this.getGoodsComment()
      this.getCartTotal()
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    }
  },
  computed: {
    goodsId () {
      return this.$route.params.id
    }
  },
  methods: {
    swiperChange (index) {
      this.current = index
    },
    async getGoodsDetail () {
      const { data: { detail } } = await this.$api.goods.getGoodsDetail(this.goodsId)
      this.detail = detail
    },
    async getGoodsService () {
      const { data: { list } } = await this.$api.goods.getGoodsService(this.goodsId)
      this.goodsService = list
    },
    async getGoodsComment () {
      const { data: { list, total } } = await this.$api.goods.getGoodsComment(this.goodsId, 3)
      this.commentList = list
      this.total = total
    },
    addFn () {
      this.mode = 'cart'
      this.showPannel = true
    },
    buyFn () {
      this.mode = 'buyNow'
      this.showPannel = true
    },
    addCart () {
      if (this.loginConfirm()) {
        this.cartAdd()
      }
    },
    async cartAdd () {
      const { data, message } = await this.$api.cart.addCart(this.detail.goods_id, this.countNum, this.detail.skuList[0].goods_sku_id)
      this.cartTotal = data.cartTotal
      this.$toast.success(message)
      this.showPannel = false
    },
    async getCartTotal () {
      const { data } = await this.$api.cart.getCartTotal()
      this.cartTotal = data.cartTotal
    },
    async goPay () {
      if (this.loginConfirm()) {
        this.$router.push({
          path: '/pay',
          query: { mode: 'buyNow', goodsId: this.detail.goods_id, goodsNum: this.countNum, goodsSkuId: this.detail.skuList[0].goods_sku_id }
        })
      }
    },
    imageChagange (e) {
      if (e.target.nodeName === 'IMG' || e.target.nodeName === 'img') {
        const currentImg = e.target.currentSrc
        const imgNode = this.$refs.descDom.querySelectorAll('img')
        this.imgs = []
        imgNode.forEach((element, index) => {
          this.imgs.push(element.currentSrc)
          if (currentImg === element.currentSrc) {
            this.imgIndex = index
          }
        })
        ImagePreview({
          images: this.imgs,
          startPosition: this.imgIndex
        })
      }
    }
  },
  beforeRouteEnter (to, from, next) {
    if (from.name === 'GoodsComment') {
      to.meta.canKeep = true
    } else {
      to.meta.canKeep = false
    }
    next()
  }
}
</script>

<style lang="less" scoped>
.van-swipe{
  .van-swipe-item{
    display: flex;
    img{
       width: 100%;
    }
  }
}
.custom-indicator {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 2px 8px;
  font-size: 14px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  color: #666;
}
.content{
  padding: 0 10px 50px;
  .price-sales{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 12px 0;
    .price-box{
        display: flex;
        align-items: center;
        .price{
            font-size: 16px;
            color: @primaryColor;
        }
        .price2{
            font-size: 13px;
            text-decoration:line-through;
            color: #999;
            margin-left: 6px;
        }
    }
    .sales{
      font-size: 14px;
      color: #999;
    }
  }
  .title{
    font-size: 15px;
    line-height: 22px;
  }
  .service-wrap{
    .service-box{
      padding: 10px 0;
      .van-cell__title{
        display: flex;
        align-items: center;
        .text{
          font-size: 15px;
        }
        .text:nth-of-type(2){
          margin-left: 10px;
        }
      }
    }
    .van-popup{
      .van-action-sheet__header{
        height: 48px;
      }
      .content{
        height: 200px;
        color: #666;
        font-size: 15px;
        line-height: 25px;
        padding-top: 10px;
        .service{
          margin-bottom: 10px;
          .title{
            font-size: 15px;
            font-weight: bold;
          }
          .text{
            font-size: 14px;
            color: #666;
          }
        }
      }
      .van-action-sheet__gap{
        display: none;
      }
      .van-action-sheet__cancel{
        background: linear-gradient(to right,#ff6034,#ee0a24);
        border-radius: 50px;
        color: #fff;
        margin: 10px;
        width: calc(100% - 20px);
      }
    }
  }
  .comment-box{
    .van-cell{
      padding: 10px 0;
      &::after{
        display: none;
      }
    }
    .list{
      .item{
        padding: 10px 0;
        .top{
          display: flex;
          align-items: center;
          justify-content: flex-start;
          .img{
            width: 30px;
            height: 30px;
            border-radius: 50%;
          }
          .name{
            font-size: 15px;
            margin: 0 10px;
          }
        }
        .con{
          font-size: 15px;
          margin: 10px 0;
        }
        .bot{
          font-size: 14px;
          color: #888;
        }
      }
    }
  }
  .desc{
    margin-top: 10px;
    /deep/ table {
      border-spacing: 0;
      a{
        display: block;
        overflow: hidden;
      }
    }
    /deep/ img{
      width: 100% !important;
      float: left;
    }
    /deep/ p{
      margin: 0;
      overflow: hidden;
    }
  }
}

.van-popup{
  .van-action-sheet__header{
    font-weight: bold;
  }
}
.van-button{
  border-radius: 50px;
}
.btn1{
  margin-right: 10px;
}
.pannel-box{
  .content{
    position: relative;
    padding: 10px 10px 90px;
    .goods{
      display: flex;
      .left{
        overflow: hidden;
        img{
          float: left;
          width: 80px;
          height: 80px;
        }
      }
      .right{
        margin-left: 12px;
        .price{
          span{
            font-size: 16px;
          }
          font-size: 20px;
          color: orange;
        }
        .stock{
          font-size: 14px;
          color: #666;
          margin-top: 3px;
        }
      }
    }
    .num-box{
      margin-top: 15px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .label{
        font-size: 14px;
      }
    }
    .btn-box{
      width: 100%;
      padding: 20px 10px 12px;
      position: absolute;
      bottom: 0;
      left: 0;
      .btn{
        width: 100%;
        height: 48px;
        border-radius: 30px;
        color: #fff;
        font-size: 16px;
        text-align:center;
        line-height: 48px;
      }
      .btn1{
        background: linear-gradient(to right,#ffd01e,#ff8917);
      }
      .btn2{
        background: linear-gradient(to right,#ff6034,#ee0a24);
      }
      .btn-none{
        background-color: #999;
      }
    }
  }
}
.van-goods-action{
  /deep/.cart-badge-none{
    .van-info{
      display: none;
    }
  }
}
</style>
